import React, { useEffect, useState } from "react";
import "./Search.css";
import Data from "../../utility/extract-data.helper";
import ResultList from "../list/ResultList";
import swal from "sweetalert";

const Search = (props) => {
  const [result, setResult] = useState([]);
  const [data, setData] = useState({});
  const [queryStr, setQueryStr] = useState("");

  useEffect(() => {
    async function fetchData() {
      await Data.fetchData();
      setData(Data.getObjects());
    }
    fetchData();
  }, []);

  const searchHandler = (e) => {
    let query = queryStr.toLowerCase();
    if (!data.has(query)) {
      swal(`No result found for ${query}. Try another word!!`);
      setResult([]);
    } else {
      let result = data.get(query);
      setResult(result);
    }
  };

  const inputChangeHandler = (e) => {
    setQueryStr(e.target.value);
  };

  return (
    <div className="container">
      <div className="App-header">
        <h1>ACME Search</h1>
      </div>
      <div className="search-container">
        <input
          onChange={inputChangeHandler}
          onKeyPress={(event) => {
            if (event.key === "Enter") {
              searchHandler();
            }
          }}
          type="text"
          className="search-input"
          placeholder="Type here"
          value={queryStr}
        />
        <button onClick={searchHandler}>Search</button>
      </div>

      <div>
        <ResultList results={result} />
      </div>
    </div>
  );
};

export default Search;
