import React from "react";
import "./ResultList.css";

const ResultList = (props) => (
  <div className="list-container">
    {props.results.map((card, idx) => (
      <div className="card">
        <h1>{card.category}</h1>
        {Object.keys(card).map((key) => {
          if (key !== "category" && key !== "matching_terms" && key !== "id") {
            if (Array.isArray(card[key])) {
              return (
                <div className="card-line">
                  <div className="line-description">{key}: </div>
                  {card[key].map((el) => (
                    <div className="line-value">{el}</div>
                  ))}
                </div>
              );
            }
            return (
              <div className="card-line">
                <div className="line-description">{key}: </div>
                <div className="line-value">{card[key]}</div>
              </div>
            );
          }
          return null;
        })}
      </div>
    ))}
  </div>
);

export default ResultList;
