import axios from "axios";

const JSON_FILES = [
  "./data/calendar.json",
  "./data/contacts.json",
  "./data/dropbox.json",
  "./data/slack.json",
  "./data/tweet.json",
];

function extractData() {
  let hashMap = new Map();

  return {
    fetchData: async () => await _helperExtractData(hashMap),
    getObjects: () => {
      return hashMap;
    },
  };
}

async function _helperExtractData(hashMap) {
  for (let file of JSON_FILES) {
    let req = await axios.get(`${file}`);
    let res = req.data[Object.keys(req.data)[0]];

    for (let data of res) {
      for (let word of data.matching_terms) {
        if (!hashMap.has(word)) {
          hashMap.set(word, []);
        }
        hashMap.get(word).push(data);
      }
    }
  }
}

export default extractData();
